let  key = 2;

export const getAll = () => {
    return {
        type: 'GET_ALL'
    }
}

export const  getTask = (id) => {
    return {
        type: 'GET_TASK',
        id: id
    }
}

export const renewAll = (id) => {
    return {
        type: 'RENEW_ALL',
        id: id
    }
}

export const addHeader = (id) => {
    return {
        type: 'ADD_HEADER',
        key: key++,
        id
    }
}

export const deleteHeader = (key) => {
    return {
        type: 'DELETE_HEADER',
        key: key
    }
}

export const selectAll = () => {
    return {
        type: 'SELECT_ALL'
    }
}
