
const initialState = {
    tasks: [
        {
            title: 1,
            id: 0,
            text: '1111111'
        },
        {
            title: 2,
            id: 1,
            text: '1111112'
        },
        {
            title: 3,
            id: 2,
            text: '1111113'
        },
        {
            title: 4,
            id: 3,
            text: '1111114'
        },
        {
            title: 5,
            id: 4,
            text: '1111115'
        },
    ],
    headers: [],
    keyCurrent: 1
}

function tasksApp(state = initialState, action) {
    switch (action.type) {
        case 'GET_ALL':
            return Object.assign(state,{
                tasks:initialState.tasks,
                headers: state.headers,
                keyCurrent: 1
            });
        case 'GET_TASK':
            return Object.assign({}, state, {
                tasks: [initialState.tasks[action.id]],
                headers: state.headers,
                keyCurrent:state.keyCurrent
            });
        case 'RENEW_ALL':
            return Object.assign({}, state, {
                tasks: state.tasks.splice(action.id, 1)
            });
        case 'DELETE_HEADER':
            let tmpArr = getHeadersArr(state.headers, action.key);
            return Object.assign({}, state,{
                headers: tmpArr,
                keyCurrent: 1
        });
        case 'ADD_HEADER':
            let existHeader = isExist(state.headers, action.id);
            let tmp=state.headers;
            if(existHeader === true) {
                tmp.push({
                    key: action.key,
                    title: state.tasks[0].title,
                    id: action.id
                });
                return Object.assign({}, state, {
                    headers: tmp,
                    keyCurrent: action.key
                })
            }else {
                return Object.assign({}, state, {
                    headers: state.headers,
                    keyCurrent: existHeader.key
                })
            }
        case 'SELECT_ALL': return Object.assign({}, state, {
            headers: state.headers,
            tasks: state.tasks,
            keyCurrent: 1
        });
        default: return state
    }
}

export default tasksApp;

function isExist(array, id) {
    for(let elem of array){
        if (elem.id === id){
            return elem;
        }
    }
    return true;
}

function getHeadersArr(arr, key){
    let tmp = [];
    for(let elem of arr){
        if (elem.key !== key){
            tmp.push(elem)
        }
    }
    return tmp;
}
