import React from 'react';
import {ListGroup, ListGroupItem} from 'react-bootstrap';

class taskInfo extends React.Component{
    constructor(props){
        super(props);
        this.changeState();

    }
    changeState(){
        this.props.onMount(this.props.match.params.id);
        this.props.getTub(this.props.match.params.id);
    }
    componentWillReceiveProps(){
        this.changeState();
        return true;
    }
    render() {
        return (<ListGroup>
            <ListGroupItem>Text: {this.props.task.text}</ListGroupItem>
            <ListGroupItem>Id: {this.props.match.params.id}</ListGroupItem>
        </ListGroup>
        )

    }

}

export default taskInfo