import { connect } from 'react-redux';
import header from "../components/header";
import {deleteHeader, selectAll} from "../actions";

const mapDispatchToProps =  {
    getAll: () => selectAll(),
    deleteHeader: (key) => deleteHeader(key)
};

const mapStateToProps = (state) => {
    return {
        headers: state.tasks.headers,
        keyCurrent: state.tasks.keyCurrent
    }
};
export const Header = connect(mapStateToProps, mapDispatchToProps)(header);

export default Header;